<template>
  <div id="course-view">
    <div class="main">
      <div class="top">
        <div class="tt">我的课程</div>

        <div class="top_operate">
          <!-- v-model="formData.key" -->
          <el-date-picker
            v-model="dateRange"
            size="small"
            type="daterange"
            align="right"
            unlink-panels
            format="yyyy-MM-dd"
            value-format="yyyy-MM-dd"
            range-separator="至"
            start-placeholder="开始日期"
            end-placeholder="结束日期"
            :picker-options="pickerOptions"
            @change="selectDateRange"
            style="width: 300px;"
          ></el-date-picker>
          <el-input placeholder="请输入内容" size="small" v-model="inputSearch" style="width:250px;">
            <el-button type="primary" slot="append" @click="searchData">搜索</el-button>
          </el-input>
        </div>
      </div>

      <div class="filter-date">
        <div class="date">

          <el-button type="primary" @click="weekDataChange(-1)">上一周</el-button>
          <div
            class="item"
            v-for="(item, index) in weekData"
            :key="index"
            :class="weekIndex===index?'active':''"
            @click="selectDate(item, index)"
          >
            <div class="info">
              <div class="day">{{weekName[index]}}</div>
              <div class="dt">{{item.date}}</div>
              <div class="nus">{{item.total}}</div>
            </div>
          </div>
          <el-button type="primary" @click="weekDataChange(1)">下一周</el-button>
        </div>
      </div>

      <div class="content">
        <div class="table">
          <common-table
            :columns="tableColumns"
            :datas="tableDatas"
            th-class="table-innerhead"
            border
            :select="true"
            :pagger="false"
            :data-id="keyIdName"
            @selectHandler="selectChange"
          >
            <template slot="ClassDate" slot-scope="scope">
              <span>{{ scope.row.ClassDate | formatDate(scope.row.ClassDate)}}</span>
            </template>
            <template slot="StartTime" slot-scope="scope">
              <span>{{ timeFormat(scope.row.StartTime) }} - {{ timeFormat(scope.row.EndTime) }}</span>
            </template>
            <template slot="State" slot-scope="scope">
              <span v-if="scope.row.State==0">未上课</span>
              <span v-if="scope.row.State==1">上课中</span>
              <span v-if="scope.row.State==2">已结束</span>
            </template>
            <template slot="oprate" slot-scope="scope" align="center">
              <el-button type="primary" size="mini" @click="courseRecord(scope.row)"
                         style="background: #6acfce;border-color:#6acfce;">课堂记录
              </el-button>
              <el-button v-if="scope.row.State === courseState.after" type="primary" size="mini"
                         @click="courseEvaluate(scope.row)">课程评价
              </el-button>
              <el-button type="primary" size="mini"
                         @click="courseQuestion(scope.row)">课程提问
              </el-button>
            </template>
          </common-table>

          <div class="personal-bottom-pager">
            <el-pagination
              class="personnal-detail-wrap-pager"
              layout="prev, pager, next"
              :current-page.sync="formData.pno"
              :page-size="formData.psize"
              :total="dataTotal"
              prev-text="上一页"
              next-text="下一页"
              @current-change="pageChange"
            />
          </div>
        </div>
      </div>

      <el-dialog title="课程评价" :visible.sync="dialogVisible" style="text-align: left;">
        <el-form ref="formComment" :model="formComment" :rules="rulesComment" size="small" label-width="80px">
          <el-form-item label="课程评分" prop="score">
            <el-input-number v-model="formComment.score" :min="1" :max="100" label="课程评分" controls-position="right"
                             :precision="0" :step="1"></el-input-number>
            分
          </el-form-item>
          <el-form-item label="课程评价" prop="comment">
            <el-input type="textarea" v-model="formComment.comment" :autosize="{ minRows: 3, maxRows: 10}"
                      placeholder="请输入课程评价"
                      maxlength="500" :show-word-limit="true"></el-input>
          </el-form-item>
          <el-form-item label="" prop="comment">
            <el-checkbox v-model="formComment.anonymous">匿名评价</el-checkbox>
          </el-form-item>
          <div style="text-align:right;">
            <el-button @click="dialogVisible = false">取消</el-button>
            <el-button :loading="btnLoading" type="primary" @click="saveForm('formComment')">
              {{formComment.id ? '更新评价' : '保存' }}
            </el-button>
          </div>
        </el-form>
      </el-dialog>

      <el-dialog title="课程提问" :visible.sync="questionTableVisible" class="dialog_chat">
        <el-tabs v-model="questionTabSelected" @tab-click="courseQuestionGet">

          <el-tab-pane v-for="(item, index) in questionTabOptions" :key="index" :label="item.label" :name="item.name">
            <!-- 提问记录 -->
            <div class="chat" :ref="'chat'+item.name">
              <div v-if="questionList && questionList.length>0" class="chat_content">
                <div v-for="(sub, k) in questionList" :key="k" class=" chat_item">
                  <div class="chat_news">
                    <div class="chat_news_name">
                      {{sub['StudentName']}}
                      <span>{{formatDate(sub['CreateTime'], 'hh:mm:ss')}}</span>
                      <i class="el-icon-delete" style="color: #F56C6C;" @click="courseQuestionConfirm(sub['QID'])"></i>
                    </div>
                    <div class="chat_news_cont">
                      <span>{{sub['QContent'] }}</span>
                    </div>
                  </div>
                </div>
              </div>
              <div v-else class="chat_content" style="text-align: center;line-height: 50px;">
                暂无提问
              </div>
            </div>

            <div class="chat_put" v-if="!item.disabled">
              <el-input type="textarea" :rows="4" placeholder="请输入提问内容" v-model="formQuestion.content" maxlength="500"
                        :show-word-limit="true">
              </el-input>
              <el-button class="chat_btn" type="primary" plain size="small" :loading="btnLoading"
                         @click="courseQuestionSave">发送
              </el-button>
            </div>

          </el-tab-pane>
        </el-tabs>
      </el-dialog>

    </div>
  </div>
</template>

<script>
  import commonTable from "@/components/commonTable";
  import list from "@/mixin/list";
  import { getWeek, formatDate } from "@/utils/utils";

  export default {
    name: "",
    components: { commonTable },
    mixins: [list],
    props: {},
    data() {
      return {
        weekCount: 0,
        weekName: [
          "星期一",
          "星期二",
          "星期三",
          "星期四",
          "星期五",
          "星期六",
          "星期日"
        ],
        weekData: [],
        weekIndex: 0,
        dateRange: [],
        pickerOptions: {
          shortcuts: [
            {
              text: "最近一周",
              onClick(picker) {
                const end = new Date();
                const start = new Date();
                start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
                picker.$emit("pick", [start, end]);
              }
            },
            {
              text: "最近一个月",
              onClick(picker) {
                const end = new Date();
                const start = new Date();
                start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
                picker.$emit("pick", [start, end]);
              }
            },
            {
              text: "最近三个月",
              onClick(picker) {
                const end = new Date();
                const start = new Date();
                start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
                picker.$emit("pick", [start, end]);
              }
            }
          ]
        },

        formData: {
          startDate: "",
          endDate: "",
          psize: 20,
          pno: 1
        },
        tableColumns: [
          {
            prop: "CourseName",
            label: "课程名称",
            width: 139
          },
          {
            prop: "ClassName",
            label: "班级",
            width: 139
          },
          {
            prop: "ClassroomName",
            label: "教室"
          },
          {
            prop: "TeacherName",
            label: "上课老师"
          },
          {
            prop: "ClassDate",
            label: "上课日期",
            slot: true,
            width: 92
          },
          {
            prop: "StartTime",
            label: "上课时间",
            slot: true,
            width: 104
          },
          {
            prop: "State",
            label: "状态",
            slot: true,
            width: 80
          },
          {
            prop: "oprate",
            label: "操作",
            slot: true,
            width: 290
          }
        ],
        tableDatas: [],
        dataTotal: 0,
        keyIdName: "CourseID",
        selectedIds: [],
        deleteLoading: false,
        inputSearch: "",

        dialogVisible: false,
        formComment: {
          id: "",
          courseID: "",
          comment: "",
          anonymous: false,
          score: 100
        },
        rulesComment: {
          comment: {
            required: true,
            min: 1,
            max: 500,
            message: "长度在 1 到 500 个字符",
            trigger: "blur"
          },
          score: [
            { required: true, message: "课程评分不能为空" },
            { type: "number", message: "课程评分必须为数字值" }
          ]
        },
        btnLoading: false,

        courseState: {
          before: 0, // 课前
          at: 1, // 课中
          after: 2 // 课后
        },
        questionList: [],
        questionTabOptions: [],
        questionTabSelected: "",
        questionTableVisible: false,
        formQuestion: {
          id: "",
          courseID: "",
          content: ""
          // anonymous: false,
        }
      };
    },
    computed: {},
    watch: {},
    created() {
      this.total = this.tableDatas.length;
      this.init();
    },
    mounted() {
    },
    methods: {
      init() {
        this.weekIndex = new Date().getDay() - 1;
        this.weekIndex = this.weekIndex === -1 ? 6 : this.weekIndex;

        this.dateRange = [];
        this.weekDataChange(0);
      },
      weekDataChange(val) {
        this.weekCount += val;
        this.weekData = [];
        for (let i = 0; i < 7; i++) {
          this.weekData.push({ date: getWeek(i, this.weekCount), total: 0 });
        }
        this.getCourseStatistics();
        this.selectDate(this.weekData[this.weekIndex], this.weekIndex);
      },
      formatDate(dateValue, format) {
        return formatDate(dateValue, format);
      },
      searchData() {
        this.tableDatas = [];
        this.$http
          .get(this.$api.StuCourseDatas, {
            startDate: this.formData.startDate,
            endDate: this.formData.endDate,
            stuid: this.$http.getStu().StudentID,
            pno: this.formData.pno,
            psize: this.formData.psize,
            estate: -1,
            key: this.inputSearch
          })
          .then(res => {
            if (res.Result && res.Datas && res.Datas.length > 0) {
              res.Datas.forEach(item => {
                this.tableDatas.push(item);
              });
            }
            if (res.Result) {
              this.dataTotal = res.RowsCount || 0;
            }
          })
          .catch(err => {
          });
      },
      pageChange(page) {
        this.formData.pno = page;
        this.searchData();
      },
      selectDate(item, index) {
        this.weekIndex = index;
        this.formData.startDate = item.date;
        this.formData.endDate = item.date;
        this.searchData();
      },
      selectDateRange() {
        if (this.dateRange.length === 2) {
          this.formData.startDate = this.dateRange[0];
          this.formData.endDate = this.dateRange[1];
          this.weekIndex = -1;
        }
      },
      getCourseStatistics() {
        this.$http
          .get(this.$api.StuCourseDatas, {
            startDate: this.weekData[0].date,
            endDate: this.weekData[6].date,
            stuid: this.$http.getStu().StudentID,
            estate: -1,
            key: this.inputSearch
          })
          .then(res => {
            if (res.Result && res.Datas && res.Datas.length > 0) {
              res.Datas.forEach(item => {
                let dateValue = formatDate(item["ClassDate"], "yyyy-MM-dd");
                let index = this.weekData.findIndex(r => r.date === dateValue);
                this.weekData[index].total += 1;
              });
            }
          })
          .catch(err => {
          });
      },
      selectChange(selected) {
        this.selectedIds = selected;
      },
      courseRecord(row) {
        //课程信息缓存在sessionStorage，关闭页面自动清除
        sessionStorage.setItem("course", JSON.stringify(row));
        sessionStorage.setItem("courseID", row["CourseID"]);
        this.$router.push("/main/studentSpace/classRecord");
      },
      courseEvaluate(row) {
        this.formComment = {
          id: "",
          courseID: row["CourseID"],
          comment: "",
          anonymous: false,
          score: 100
        };
        this.getCourseEvaluate();
        this.dialogVisible = true;
      },
      getCourseEvaluate() {
        let reqData = {
          courseID: this.formComment.courseID,
          studentID: this.$http.getStu().StudentID
        };
        this.$http
          .get(this.$api.CourseCommentData, reqData)
          .then(res => {
            if (res.Result && res.Data) {
              this.formComment.id = res.Data["CCID"];
              this.formComment.comment = res.Data["Comment"];
              this.formComment.score = res.Data["Score"];
              this.formComment.anonymous = res.Data["IsAnonymous"] ? true : false;
            }
          });
      },
      saveForm(formName) {
        this.$refs[formName].validate(valid => {
          if (valid) {
            this.btnLoading = true;
            let reqData = {
              ccID: this.formComment.id,
              courseID: this.formComment.courseID,
              studentID: this.$http.getStu().StudentID,
              score: this.formComment.score,
              comment: this.formComment.comment,
              isAnonymous: this.formComment.anonymous ? 1 : 0,
              type: 2
            };
            this.$http
              .post(this.$api.CourseCommentSave, reqData)
              .then(res => {
                if (res.Result) {
                  this.dialogVisible = false;
                  this.$message({
                    message: "提交成功！",
                    type: "success"
                  });
                } else if (res.message) {
                  this.$message({
                    showClose: true,
                    ...res
                  });
                }
                this.btnLoading = false;
              })
              .catch(err => {
                this.btnLoading = false;
              });
          } else {
            this.$message({
              showClose: true,
              message: "请输入正确格式",
              type: "error"
            });
            return false;
          }
        });
      },

      // 课程提问
      courseQuestion(row) {
        this.formQuestion.courseID = row["CourseID"];
        let state = row['State'];
        if (state === this.courseState.before) {
          this.questionTabOptions = [
            { name: "1", label: "预习", disabled: false }
          ];
        } else if (state === this.courseState.at) {
          this.questionTabOptions = [
            { name: "1", label: "预习", disabled: true },
            { name: "2", label: "课中", disabled: true }
          ]
        } else if (state === this.courseState.after) {
          this.questionTabOptions = [
            { name: "1", label: "预习", disabled: true },
            { name: "2", label: "课中", disabled: true },
            { name: "3", label: "复习", disabled: false }
          ]
        }
        this.questionTabSelected = this.questionTabOptions[state].name;
        this.courseQuestionGet();
        this.questionTableVisible = true;
      },
      courseQuestionGet() {
        this.formQuestion.content = "";
        this.questionList = [];
        this.$http
          .get(this.$api.GetQuestionDatas, {
            courseID: this.formQuestion.courseID,
            studentID: this.$http.getStu().StudentID,
            attribute: this.questionTabSelected
          })
          .then(res => {
            if (res.Result && res.Datas && res.Datas.length > 0) {
              res.Datas.forEach((item, index) => {
                this.questionList.push(item);
              });
              // 滚动到底部
              let name = this.questionTabSelected;
              setTimeout(() => {
                this.$refs["chat" + name][0].scrollTop = this.$refs["chat" + name][0].scrollHeight;
              }, 100);
            }
          });
      },
      courseQuestionSave() {
        if (!this.formQuestion.content) {
          this.$message({
            showClose: true,
            message: "请输入提问内容",
            type: "error"
          });
          return false;
        } else {
          // console.log(this.questionTabOptions.find(r => r.value === this.questionTabSelected));
          let reqData = {
            qID: this.formQuestion.id,
            courseID: this.formQuestion.courseID,
            studentID: this.$http.getStu().StudentID,
            attribute: this.questionTabSelected,
            qContent: this.formQuestion.content
          };
          this.btnLoading = true;
          this.$http
            .post(this.$api.questionDataSave, reqData)
            .then(res => {
              if (res.Result) {
                this.$message({
                  message: "提交成功！",
                  type: "success"
                });
                this.courseQuestionGet();
              } else if (res.message) {
                this.$message({
                  showClose: true,
                  ...res
                });
              }
              this.btnLoading = false;
            })
            .catch(err => {
              this.btnLoading = false;
            });
        }
      },
      courseQuestionConfirm(id) {
        this.$confirm('确认删除此提问?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.courseQuestionDelete(id);
        }).catch(() => {
        });
      },
      courseQuestionDelete(id) {
        this.$http
          .post(this.$api.questionDataDelete, { ids: id })
          .then(res => {
            if (res.Result) {
              this.$message({
                message: "删除成功！",
                type: "success"
              });
              this.courseQuestionGet();
            } else if (res.message) {
              this.$message({
                showClose: true,
                ...res
              });
            }
          })
          .catch(err => {

          });
      },

      timeFormat(val) {
        if (val.length < 8)
          return val;

        return val.substring(0, val.length - 3);
      }
    },
  };
</script>

<style lang='less' scoped>
  #course-view {
    width: 956px;

    .main {
      height: 100%;
      background: #fff;
      padding: 20px;

      .ketajlu {
        background: #6acfce;
      }

      .top {
        display: flex;
        justify-content: space-between;
        margin-bottom: 30px;

        .tt {
          font-size: 14px;
          font-family: MicrosoftYaHei;
          font-weight: 400;
          color: rgba(51, 51, 51, 1);
          margin-top: 5px;
        }

        .top_operate {
          float: right;
        }
      }

      .filter-date {
        margin-bottom: 15px;
        display: flex;
        justify-content: space-between;

        .date {
          width: 100%;
          height: 50px;
          // border: 1px solid #000;
          background: #ecf1f7;
          display: flex;
          justify-content: space-between;

          .active {
            background: #2a9efc !important;

            .info {
              .day,
              .dt {
                color: #fff !important;
                background: #2a9efc !important;
              }

              .nus {
                border: 1px solid #fff;
              }
            }
          }

          .item {
            position: relative;
            width: 14.28%;
            display: flex;
            align-items: center;
            justify-content: center;

            .info {
              .day {
                font-size: 16px;
                color: #333;
              }

              .dt {
                font-size: 12px;
                color: #666;
                line-height: 22px;
              }
            }
          }

          .nus {
            position: absolute;
            top: 5px;
            right: 5px;
            width: 24px;
            height: 12px;
            background: rgba(42, 158, 252, 1);
            border-radius: 6px;
            font-size: 10px;
            color: #fff;
            line-height: 12px;
          }
        }
      }

      .personal-bottom-pager {
        text-align: right;
        user-select: none;
        margin-top: 25px;

        .btn-next,
        .btn-prev,
        .el-pager li {
          border: 1px solid #dfdfdf;
          color: #666;
          font-weight: 400;
        }

        .el-pager li {
          border-left: none;
        }

        .el-pager li.active {
          background: #409eff;
          color: #fff;
          border-color: #409eff;
        }

        .btn-next,
        .btn-prev {
          padding: 0 5px;
        }

        .btn-prev {
          border-top-left-radius: 3px;
          border-bottom-left-radius: 3px;
        }

        .btn-next {
          border-top-right-radius: 3px;
          border-bottom-right-radius: 3px;
          border-left: none;
        }
      }
    }
  }

  .dialog_chat {
    text-align: left;

    /deep/ .el-dialog__body {
      padding-top: 10px;
    }
  }

  /*聊天窗*/
  .chat {
    padding: 0 5px;
    text-align: left;
    width: 100%;
    min-height: 50px;
    max-height: 450px;
    overflow-y: auto;
    background: rgba(10, 10, 10, 0.1);
    border: 1px solid #DCDFE6;

    .chat_content {
      .chat_item {
        display: flex;
        justify-content: flex-start;
        align-items: flex-start;

        .chat_news {
          .chat_news_name {
            font-size: 12px;
            line-height: 2.5;
            color: #409EFF;
          }

          .chat_news_cont {
            padding: 6px 5px 6px 18px;
            font-size: 14px;
            line-height: 1.5;

            text-align: left;
            word-break: break-all;
          }
        }
      }

    }
  }

  /*聊天输入框*/
  .chat_put {
    position: relative;
    /deep/ .el-textarea .el-input__count {
      right: 70px;
    }
    .chat_btn {
      position: absolute;
      bottom: 5px;
      right: 5px;
    }
  }

  /deep/ .el-range-separator {
    width: 35px;
  }
</style>



